.board-wrapper {
    
    display: flex;
    flex-wrap: wrap;
    row-gap: 4px;
    column-gap: 4px;
    padding: 4px;
    justify-content: center;
    align-items: center;
    border-radius: 2px;
}

.field {
    width: 98px;
    height: 98px;
    background: white;
    border-radius: 2px;
}

.plain {
    background: #ADC178;
}

.dog {
    width: 98px;
    height: 98px;
    background: #A98467;
    border-radius: 10px;
}

.field2 {
    width: 112px;
    height: 112px;
    background: lightgrey;
    border-radius: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.board2 {
    width: 500px;
    display:flex;
    flex-wrap: wrap;
    row-gap: 4px;
    column-gap: 4px;
    padding: 4px;
    justify-content: center;
    align-items: center;
    border-radius: 2px;
}

.cat {
    width: 98px;
    height: 98px;
    background: #6C584C;
    border-radius: 10px;
}

.bird {
    width: 98px;
    height: 98px;
    background: #DDE5B6;
    border-radius: 16px;
}

.empty {
    width: 98px;
    height: 98px;
    background: transparent;
    border-radius: 2px;
}

.field-board {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    row-gap: 4px;
    column-gap: 4px;
    padding: 4px;
    justify-content: center;
    align-items: center;
    width: 1016px;
    height: 608px;
}

.unit-board {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    row-gap: 4px;
    column-gap: 4px;
    padding: 4px;
    justify-content: center;
    align-items: center;
    width: 1016px;
    height: 608px;
}

.selected {
    border: 4px solid red;
    animation: selectedBorder .6s ease-in-out infinite;
}

.over {
    transition: all;
    transform: scale(1.1);
}

@keyframes selectedBorder {
    0% {border: 4px solid rgba(255, 0, 0, .2);}
    50% {border: 4px solid rgba(255, 0, 0, 1);}
    100% {border: 4px solid rgba(255, 0, 0, .2);}
  }

/* units styling */

.unit-name {
    text-align: center;
    font-size: 2em;
    margin: 0;
    padding: 0;
}

.unit-stats {
    text-align: center;
    font-size: 1.5em;
    margin: 15px;
    padding: 0;
}
